import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import {
  userSignOut,
  hideMessage,
  showAuthLoader,
  userSignIn
} from "../../../actions/Auth";
import { connect } from "react-redux";
import { auth } from "../../../firebase/firebase";
import logo from "../../../assets/img/brand/logo.png";
import CircularProgress from "../../../components/CircularProgress/index";
import { LANGUAGE_CODES, DEFAULT_LANGUAGE } from '../../../constants/AppConstant';

class Login extends Component {
  constructor(props) {
    super(props);

    localStorage.defaultLanguage = DEFAULT_LANGUAGE;

    this.state = {
      usernameOrEmail: '',
      email: '',
      password: '',
      lang: DEFAULT_LANGUAGE
    };
  }  

  handleSubmit = (e) => {
    e.preventDefault();
    if (!this.state.usernameOrEmail || !this.state.password) return;
    this.props.showAuthLoader();
    this.props.hideMessage();
    this.props.userSignIn({ username: this.state.usernameOrEmail, email: this.state.usernameOrEmail, password: this.state.password });
  };

  componentDidUpdate() {
    if (this.props.showMessage) {
      setTimeout(() => {
        this.props.hideMessage();
      }, 60000);
    }
    if (this.props.authUser !== null) {
      localStorage.loginEmail = auth.currentUser ? auth.currentUser.email : 'admin';
      localStorage.defaultLanguage = this.state.lang;
      this.props.history.push('/');
    }
  }

  render() {
    const { showMessage, loader } = this.props;

    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="4">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form onSubmit={this.handleSubmit}>
                      <div className="text-center">
                        <img src={logo} alt="GMTCore Logo" className="login-logo" />
                      </div>     
                      <h2>Đăng nhập</h2>
                      {showMessage ? (<div className="alert alert-danger">Tên đăng nhập/Email hoặc mật khẩu không đúng</div>) : (<p className="text-muted">Đăng nhập với tài khoản của bạn</p>)}
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Tên đăng nhập hoặc email" autoComplete="usernameOrEmail" value={this.state.usernameOrEmail} onChange={e => this.setState({ usernameOrEmail: e.target.value })} />
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Mật khẩu" autoComplete="current-password" value={this.state.password} onChange={e => this.setState({ password: e.target.value })} />
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend" className="language-icon-box">
                          <InputGroupText>
                            <img className="language-flag" src={require('../../../assets/flags/' + this.state.lang + '.png')} alt={this.state.lang} />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="select" placeholder="Ngôn ngữ" autoComplete="current-language" value={this.state.lang} onChange={e => this.setState({ lang: e.target.value })}>
                          {
                            LANGUAGE_CODES.map((l, i) => {
                              return (
                                <option key={i} value={l.code}>
                                  {l.name}
                                </option>
                              )
                            })
                          }
                        </Input>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4">Đăng nhập</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Link to="/password/recover">
                            <Button color="link" className="px-0">Quên mật khẩu?</Button>
                          </Link>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>                
              </CardGroup>
            </Col>
          </Row>
        </Container>
        {loader ?
          <div className="gx-loader-view">
            <CircularProgress />
          </div> : null}
      </div>
    );
  }
}

const mapStateToProps = ({ auth }) => {
  const { loader, authUser, showMessage } = auth;
  return { loader, authUser, showMessage };
};

export default connect(mapStateToProps, {
  userSignIn,
  hideMessage,
  showAuthLoader,
  userSignOut
})(Login);
